<template>
    <v-container fluid grid-list-md>
        <v-layout row wrap class="ui-insect-list-a ui-insect-bicho-lixeiro">
            <v-flex md7>
                <bugCard :insetos="insetos" />
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>

import bugCard from "@/components/bugCard";

export default {
    name: 'BichoLixeiro',
    components: {
        bugCard
    },
    data () {
      return {
        insetos: [],
        show: false,
        active_tab: 0,
        tabs: null
      }
    },
    computed: {
    },
    methods: {
        getBugs() {
            axios.get('./idbug-data-bicho-lixeiro.json')
            .then(response => {
                this.insetos = response.data.bugs;
            })
            .catch(e => {
                console.log('error!.data')
                this.errors.push(e)
            })
        }
    },
    mounted() {
        this.getBugs();
    },
    created() {
    }
};
</script>
